<template>
	<div class="mine-order-details">
		<div class="order-details-title">{{title}}</div>
		<div class="good-detail">
			<div class="good-img">
				<img src="../assets/clothes-bg.png">
			</div>
			<div class="goods-label">
				<p class="good-title"></p>
				<p class="good-size">L</p>
				<p class="good-price">89.00</p>
			</div>
		</div>

		<ul class="deals-list">
			<li class="deals-items">
				<div class="deals-title"></div>
				<div class="deals-value">0.00</div>
			</li>
			<li class="deals-items">
				<div class="deals-title"></div>
				<div class="deals-value">89.00</div>
			</li>
		</ul>

		<div class="phone">(400-8303220)</div>

		<div class="nearby-machine" v-show="mapShow">
			<p class="title"></p>
			<div class="map" id="container" style="height:9.5rem"></div>
			<div class="location">
				<img src="../assets/ico-location.png">
				<span></span>
			</div>
		</div>

		<div class="order-create-details">
			<div class="order-num">2017325046254</div>
			<div class="order-ctime">2017-09-11 14:45:45</div>
			<div v-if="type3">
				<div class="pay-time">2017-09-11 14:45:45</div>
				<div class="pick-time">2017-09-11 14:45:45</div>
				<div class="machines-num">658746156846</div>
				<div class="machines-location"></div>
			</div>
		</div>

		<div class="bottom-bar">
			<div class="btn1" id="btn1">{{btn1}}</div>
			<router-link tag="div" to="/CompletePay" class="btn2" v-if="btn2">{{btn2}}</router-link>
		</div>
	</div>
</template>

<script>
import AMap from 'AMap'
import avatar from '@/assets/ico-box-location.png'
export default {
	name: 'MineOrderDetails',
	data () {
		return {
			mapShow:false,
			title:"",
			btn1:"",
			btn2:"",
			type3:false
		}
	},
	created (){
		document.title="";
		window.scrollTo(0, 0);
	},
	mounted () {
		if (this.$route.params.type==1||this.$route.params.type==2) {
			if (this.$route.params.type==1) {
				this.title="";
				this.btn1="";
				this.btn2="";
			}else{
				this.title="";
				this.btn1="";
				this.btn2="";
			}
			this.mapShow=true;
			this.init();
		}else if (this.$route.params.type==3){
			this.title="";
			this.btn1="";
			this.btn2=null;
			this.type3=true;
			var _this = this;
			$("#btn1").click(function() {
				_this.$router.push({ path: '/AfterSales' });
			});
		}else if (this.$route.params.type==4){
			this.title="";
			this.btn1="";
			this.btn2=null;
			var _this = this;
			$("#btn1").click(function() {
				_this.$router.push({ path: '/RefundDetails' });
			});
		}
	},
	methods: {
		init: function () {
			var map, geolocation;
			//
			map = new AMap.Map('container', {
				resizeEnable: true,
				zoom: 11,
			});
			map.plugin('AMap.Geolocation', function() {
				geolocation = new AMap.Geolocation({
					enableHighAccuracy: true,//:true
					timeout: 10000,			//10
					buttonOffset: new AMap.Pixel(0, 0),//Pixel(10, 20)
					buttonPosition:'RB'
				});
				map.addControl(geolocation);
				geolocation.getCurrentPosition();
				AMap.event.addListener(geolocation, 'complete', onComplete);//
				AMap.event.addListener(geolocation, 'error', onError);	//
			});
			function onComplete(data) {
				alert('');
				// str.push('' + data.position.getLng());
				// str.push('' + data.position.getLat());
			}
			function onError(data) {
				alert('')
			}
			var icon = new AMap.Icon({
				image : avatar,//24px*24px
				size : new AMap.Size(24,24)
			});
			var marker = new AMap.Marker({
				icon : icon,//24px*24px
				position : [113.270541,23.077351],
				offset : new AMap.Pixel(-12,-12),
				map : map
			});
		}
	}
}
</script>

<style scoped>
	.order-details-title{
		padding: 0.5rem 1.5rem;
		background: url(../assets/ico_text.png) left no-repeat;
		background-size: auto 1rem;
		margin: 0 1rem;
		font-size: 0.875rem;
	}
	.good-detail{
		display: flex;
		display: -webkit-flex;
		padding: 1rem;
	}
	.good-img{
		width: 5rem;
		height: 5rem;
		padding: 0.2rem;
		margin-right: 1rem;
	}
	.good-title{
		padding: 0.2rem 0;
	}
	.good-size{
		color:#999;
		font-size: 0.75rem;
	}
	.good-price{
		padding: 1rem 0;
	}
	.nearby-machine .title{
		padding: 1rem;
	}
	.nearby-machine .location{
		font-size: 0.875rem;
		color: #666;
		padding: 0.5rem;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
	}
	.nearby-machine .location img{
		width:0.7rem;
		margin-right: 0.5rem;
	}
	.deals-list{
		padding: 0rem 1rem;
		font-size: 0.875rem;
	}
	.deals-items{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.phone{
		width: 46%;
		white-space:nowrap;
		padding-left: 1.5rem;
		margin: 1rem auto;
		font-size: 0.875rem;
		background: url(../assets/ico_phone.png) left no-repeat;
		background-size: auto 1rem;
	}
	.order-create-details{
		font-size: 0.875rem;
		color: #666;
		margin-top: 1rem;
		padding-left: 1rem;
	}
	.bottom-bar{
		width: 100%;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: flex-end;
		-webkit-justify-content: flex-end;
		position: fixed;
		left: 0;
		bottom: 0;
		padding: 0.5rem;
		box-sizing: border-box;
		font-size: 0.875rem;
	}
	.bottom-bar>div{
		border: 1px solid #000;
		padding: 0.2rem;
		width: 4.5rem;
		text-align: center;
		border-radius: 0.25rem;
	}
	.bottom-bar .btn2{
		background-color: #000;
		color: #fff;
		margin-left: 0.5rem;
	}
</style>



