<template>
	<div class="designer-detail">
		<div class="designer-intro">
			<div class="designer-intro-header">
				<img src="../assets/ico_header.png">
				<div class="designer-laber">PRO</div>
			</div>
			<h3 class="designer-intro-name"></h3>
			<div class="designer-intro-content">
				<p></p>
				<div class="designer-intro-detail" :class="{allShow:isActive}"></div>
				<div class="pull-down-btn" :class="{active:isActive}" @click="isActive=!isActive"></div>
			</div>
		</div>
		<div class="designer-works">
			<h4 class="designer-works-title">TA 10</h4>
			<ul class="goods-list">
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="goods-items">
					<div class="goods-details">
						<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
						<span class="goods-title"></span>
						<p class="goods-price">10</p>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="goods-items">
					<div class="goods-details">
						<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
						<span class="goods-title"></span>
						<p class="goods-price">10</p>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="goods-items">
					<div class="goods-details">
						<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
						<span class="goods-title"></span>
						<p class="goods-price">10</p>
					</div>
				</router-link>
				<router-link tag="li" :to="{ path: '/GoodsDetail/gid/1'}" class="goods-items">
					<div class="goods-details">
						<div><img class="goods-logo" src="../assets/goods-logo.png"></div>
						<span class="goods-title"></span>
						<p class="goods-price">10</p>
					</div>
				</router-link>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
	name: 'designer-detail',
	data () {
		return {
			msg:this.$route.params.did,
			isActive:false
		}
	},
	mounted:function(){
		document.title="";
		// this.$nextTick(function(){
		// 	this.designerPage();
		// })
	},
	methods:{

	},
}
</script>
<style scoped>
	.goods-list{display: flex;display: -webkit-flex;flex-wrap: wrap;-webkit-flex-wrap: wrap;justify-content: space-between;-webkit-justify-content: space-between;}
	.goods-items{width: 48%;padding: 1.5rem 1.5rem 0;font-size: 0.75rem;box-sizing: border-box;flex-shrink:0;-webkit-flex-shrink:0;text-align: center;position: relative;}
	.designer-laber{font-size: 0.75rem;background-color: #000;color: white;padding: 0 0.2rem;position: absolute;top: 0;right: 0;}
	.goods-details{background: url("../assets/clothes-bg.png") top no-repeat;background-size: 100% auto;}
	.goods-logo{width: 42%;margin: 2rem;}
	.designer-detail{
		text-align: center;
		padding: 0.8rem;
	}
	.designer-intro-header{
		width: 5rem;
		position: relative;
		margin: auto;
	}
	.designer-intro-header img{
		display: block;
	}
	.designer-laber{
		top: auto;
		bottom: 0;
	}
	.designer-intro-name{
		padding: 0.8rem;
	}
	.designer-intro-content{
		font-size: 0.875rem;
		color: #999;
	}
	.designer-intro-content div{
		padding: 0.8rem 1.5rem;
	}
	.designer-intro-detail{
		transition: all 1s ease;
		max-height: 1.5rem;
/*		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;*/
		overflow: hidden;
	}
	.allShow{
		transition: all 1s ease;
		max-height: 6rem;
		/*height: auto;*/
		/*display: block;*/
	}
	.pull-down-btn{
		height: 1rem;
		background: url("../assets/ico-down.png") center no-repeat;background-size: auto 30%;
	}
	.active{
		height: 1rem;
		background: url("../assets/ico-up.png") center no-repeat;background-size: auto 30%;
	}
	.designer-works-title{
		border-top: 1px solid #e5e5e5;
		border-bottom: 1px solid #e5e5e5;
		padding: 0.8rem;
	}
</style>



