<template>
	<div class="goods-detail">
		<div class="goods-detail-page">
			<div class="swiper-container">
		        <div class="swiper-wrapper">
					<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
					<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
					<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
		        </div>
				<!-- Add Pagination -->
				<div class="swiper-pagination"></div>
		    </div>
		    <div class="goods-label">
		    	<h3 class="goods-title">{{productData.product_title}}</h3>
		    	<h3 class="goods-price">{{productData.product_price}}</h3>
		    	<div class="goods-intro"></div>
		    </div>
		    <ul class="select-list">
		    	<li class="select-item" @click="isShow=true;"></li>
		    	<router-link tag="li" :to="{ path: '/DesignerDetail/did/1'}" class="designer-item">
		    		<div class="item-left">
		    			<div class="designer-header"><img src="../assets/ico_header.png"></div>
		    			<div>
							<h4 class="designer-name"></h4>
							<span class="designer-lable"></span>
		    			</div>
		    		</div>
		    		<div class="item-right"></div>
		    	</router-link>
		    </ul>
		    <div class="graphic-details"><img src="../assets/graphic.png"></div>
		</div>

		<div class="size-page-bg" v-show="isShow"></div>
		<transition name="slide-fade">
			<div class="size-page" v-show="isShow">
				<div class="size-wrap">
					<div class="goods-img"><img src="../assets/clothes-bg.png"></div>
					<div class="close-btn" @click="isShow=!isShow"><img src="../assets/ico-close.png"></div>
					<div class="size-goods-label">
						<p class="size-goods-price">{{productData.product_price}}</p>
						<p class="size-goods-title">{{productData.product_title}}</p>
					</div>
					<ul class="size-list">
						<li class="size-item">
							<div class="size-item-title"></div>
							<span class="size-item-value"></span>
						</li>
						<li class="size-item">
							<div class="size-item-title"></div>
							<span class="size-item-value">T</span>
						</li>
						<li class="size-item">
							<div class="size-item-title"></div>
							<span class="size-item-value"></span>
						</li>
						<li class="size-item">
							<div class="size-item-title"></div>
							<div class="size-item-value">
								<span :class="[{isSelect:active==index},{last:(index+1)%4==0}]" v-for="(value,index) in sizeList" @click="active=index">{{value}}</span>
							</div>
						</li>
					</ul>
				</div>
				<div class="bottom-bar" @click="selectHideFn"></div>
			</div>
		</transition>

		<div class="bottom-bar" v-if="!isShow">
			<div class="collect" :class="{active:isActive}" @click="isActive=!isActive"></div>
			<div class="buy" @click="buyFn"></div>
		</div>
	</div>
</template>

<script>
import Swiper from '../../static/js/swiper.min.js'
export default {
	name: 'goods-detail',
	data () {
		return {
			sizeList:["S","M","L","XL"],
			active:null,
			isShow:false,
			isActive:false,
			isSelect:false,
			productData:""
		}
	},
    created (){
		document.title="";
		window.scrollTo(0, 0);
	},
	mounted:function(){
		console.log(this.$route.params.gid)
		CF.showLoading(1);
		var swiper = new Swiper('.swiper-container', {
			pagination: '.swiper-pagination',
			paginationClickable: true
	    });
	    this.getDataFn();
	},
	methods:{
		getDataFn(){
			axios.get("/api/product/"+this.$route.params.gid).then(res=>{
				console.log(res)
				if (res.data.code==1001) {
					CF.showLoading(false);
					this.productData=res.data.response.data;
					console.log(this.productData)
				}
			})
		},
		selectHideFn(){
			this.isShow=false;
			if (this.isSelect) this.$router.push({path:'/ConfirmOrder'});
		},
		buyFn(){
			if (this.active==null) {
				this.isShow=true;
			}else{
				this.$router.push({path:'/ConfirmOrder'});
			}
		}
	},
}
</script>

<style scoped>
	.slide-fade-enter-active,.slide-fade-leave-active{
		transition: all .6s ease;
	}
	.slide-fade-enter, .slide-fade-leave-to{
		transform: translateY(500px);
	}
	.isSelect{
		background-color: #000;
		color: #fff;
	}
	.goods-detail-page{
		position: relative;
		z-index: 1;
		margin-bottom: 3rem;
	}
	.swiper-slide{
		text-align: center;
	}
	.swiper-slide img{
		width: 64%;
		margin: 2rem 0 3rem;
	}
	.goods-label{
		padding: 0.8rem;
	}
	.goods-label .goods-price{
		padding: 0.5rem 0;
		color: #666;
	}
	.goods-label .goods-intro{
		color: #999;
	}
	.select-list{
		padding: 0.8rem;
	}
	.select-list li{
		height: 3rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		display: -webkit-flex;
		-webkit-align-items: center;
		-webkit-justify-content: space-between;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		font-size: 0.875rem;
	}
	.select-list .select-item{
		border-top: 1px solid #e5e5e5;
	}
	.select-list .designer-item{
		padding: 0.5rem 0;
		font-size: 0.75rem;
	}
	.select-list .item-left{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
	}
	.select-list .item-right{
		margin-right: 1rem;
	}
	.select-list .designer-header{
		width: 2.5rem;
		height: 2.5rem;
		border-radius: 5rem;
		margin-right: 0.5rem;
	}
	.select-list .designer-name{
		font-size: 0.875rem;
	}
	.select-list .designer-lable{
		color: #666;
	}
	.select-list .designer-header img{
		display: block;
	}
	.size-page{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2;
		overflow: hidden;
	}
	.size-page-bg{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2;
		background-color: black;
		opacity: 0.5;
	}
	.size-wrap{
		position: relative;
		margin-top: 10rem;
		padding: 1rem;
		background-color: #fff;
		height: 64%;
	}
	.goods-img{
		width: 7rem;
		padding: 0.5rem;
		box-sizing: border-box;
		background-color: #fff;
		border: 1px solid ;
		position: absolute;
		top: -1.5rem;
		left: 1rem;
	}
	.close-btn{
		width: 1rem;
		padding: 1rem;
		position: absolute;
		right: 0;
		top: 0;
	}
	.size-goods-label{
		padding: 1.2rem 8rem;
	}
	.size-item span{
		width: 5rem;
		line-height: 2rem;
		margin: 0.8rem 0.8rem 0.8rem 0;
		text-align: center;
		display: inline-block;
		border:1px solid black;
		box-sizing: border-box;
	}
	.size-item span.last{
		margin: 0.8rem 0;
	}
	span.size-item-value{
		background-color: #000;
		color: #fff;
	}
	.bottom-bar{
		width: 100%;
		height: 3rem;
		line-height: 3rem;
		display: flex;
		display: -webkit-flex;
		color: #fff;
		position: fixed;
		left: 0;
		bottom: 0;
		text-align: center;
		z-index: 99;
		background-color: #fff;
	}
	.bottom-bar div{
		width: 50%;
	}
	.bottom-bar .collect{
		background: url("../assets/collect1.png") center no-repeat;
		background-size: auto 1.8rem;
	}
	.bottom-bar .active{
		background: url("../assets/collect2.png") center no-repeat;
		background-size: auto 1.8rem;
	}
	.bottom-bar .buy{
		background-color: #000;
	}
	.size-page .bottom-bar{
		display: block;
		background-color: #000;
	}
</style>





