<template>
	<div class="demo">
		<div id="container" style="width:500px; height:300px"></div>
	</div>
</template>

<script>
import AMap from 'AMap'

export default {
	name: 'demo',
	data () {
		return {
			msg:""
		}
	},
	mounted () {
		this.init()
	},
    methods: {
      init: function () {
        const map = new AMap.Map('container', {
          // center: [116.397428, 39.90923],
          // resizeEnable: true,
          zoom: 11
        })
        AMap.plugin('AMap.Geolocation', function () {
		   const geolocation = new AMap.Geolocation({
		        enableHighAccuracy: true,//:true
		        timeout: 10000,          //10
		        maximumAge: 0,           //00
		        convert: true,           //true
		        showButton: true,        //true
		        buttonPosition: 'LB',    //'LB'
		        buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
		        showMarker: true,        //true
		        showCircle: true,        //true
		        panToLocation: true,     //true
		        zoomToAccuracy:true      //false
		    });
		    AMap.addControl(geolocation);
		    geolocation.getCurrentPosition();
		    AMap.event.addListener(geolocation, 'complete', onComplete);//
		    AMap.event.addListener(geolocation, 'error', onError);      //
		});
      }
    }
}
</script>

<style scoped>

</style>



