<template>
	<div class="mine-works-details">
		<div class="works-icon">
			<img src="../assets/goods-logo.png">
		</div>
		<div class="works-introduce">
			<div class="works-title"></div>
			<div class="works-sales">(5)</div>
			<div class="works-describe">v</div>
			<div class="works-price">89.00</div>
		</div>
		<div class="graphic-details"><img src="../assets/graphic.png"></div>

		<div class="space"></div>
		<div class="btn-bottom">
			<div class="btn-left"></div>
			<div class="btn-right"></div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'MineWorksDetails',
	data () {
		return {

		}
	},
	created (){
		document.title="";
		window.scrollTo(0, 0);
	},
	mounted () {
		
	},
	methods: {

	}
}
</script>

<style scoped>
	.works-icon{
		width: 100%;
		height: 22rem;
		background: url("../assets/clothes-bg.png") center no-repeat;
		background-size: auto 100%;
		margin: 1rem 0 0.5rem;
		position: relative;
	}
	.works-icon img{
		width: 38%;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		margin: 5rem auto;
	}
	.works-introduce{
		text-align: center;
		font-size: 0.875rem;
		padding: 1rem;
	}
	.works-title,.works-price{
		font-size: 1rem;
		font-weight: bold;
	}
	.works-sales{
		color: #666;
		margin: 0.5rem 0;
	}
	.works-describe{
		color: #999;
		margin-bottom: 0.5rem;
	}
	.space{
		height: 5rem;
	}
	.btn-bottom{
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
		display: flex;
		display: -webkit-flex;
		background-color: #fff;
	}
	.btn-bottom div{
		width: 50%;
		margin: 1rem;
		text-align: center;
		border:1px solid #000;
		border-radius: 0.25rem;
		line-height: 2rem;
	}
	.btn-right{
		color: #fff;
		background-color: #000;
	}
</style>



